
.content_user{
  padding-top: 20px;
  
}

.tab-label-title {
  color: var(--hxrymz_color_base_label);
  font-weight: 300;
  line-height: 1;
  font: 600 32px/34px Open Sans,sans-serif;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  font-family: Google Sans,Roboto,Arial,sans-serif;
  margin: 0 0 25px 0;
  padding: 0;
}


.filter--cards.logs-blocks {
  max-width: 85vw;
}



.__message__{
  font: 400 11px/26px "Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;
  max-width: 75vw;
}

.option--wrapper .__action_options-- .__descr__._email{
  text-transform: lowercase;
} 


.__action_options--.__active .__srvarea__filters__._order_detail_{
  transform: translate3d(0, 0%, 0);
}

.filter--cards .top--secction .actions .date_expire {
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  max-height: 60px;
  -webkit-transition: opacity ease .08s, max-height ease .08s, -webkit-transform ease .28s;
  transition: opacity ease .08s, max-height ease .08s, -webkit-transform ease .28s;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s, -webkit-transform ease .28s;
  cursor: pointer;
  padding: 10px 0px;
  font: 500 13px/19px "Roboto","Arial","Open Sans",sans-serif;
  color: var(--hxrymz_color_base_hover);
  text-align: right;
}



.runnig--secction_{
  padding: 20px 7px 0px;
}

.active--secction .actions--icons.active svg{
  fill:var(--hxrymz_color_base_hover);
} 

.active--secction .actions--icons.disable svg{
  fill:#888;
} 





.filter--cards .active--secction .actions--icons {
  width: 20%;
  max-height: 60px;
  min-height: 60px;
  -webkit-transition: width ease .28s, opacity ease .3s;
  transition: width ease .28s, opacity ease .3s;
}


.filter--cards .active--secction .actions--icons svg {
  fill: var(--hxrymz_color_base_hover);
  cursor: pointer;
}


.filter_option{
  padding: 10px 20px 0px;
}

.filter_option svg{
  fill: #888;
}


.actions .date_expire svg{
  fill: var(--hxrymz_color_base_label);
}



.settings--wrapper{
  background-color: var(--hxrymz_panel_background_color);
  border-top-left-radius: 13px;
  border-top-right-radius: 13px;
  min-height: 70vh;
}

.settings--wrapper .__header-- {
  padding: 17px 13px;
  border-bottom: 1px solid var(--hxrymz_color_base_label);
  display: flex;
}

.settings--wrapper .__header-- 
h5 {
    display: block;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font: 500 17px/26px "Roboto","Arial","Open Sans",sans-serif;
    color : var(--hxrymz_color_base_label); 
}

.settings--wrapper .__body-- {
  padding: 13px 3px;
}

.settings--wrapper .__items_setting {
  padding: 4px 13px;
  background-color : var(--hxrymz_panel_background_color);
  display: flex;
}

.settings--wrapper .__items_setting svg {
  padding: 7px 13px;
  fill : var(--hxrymz_color_base_label); 
}

.settings--wrapper .__items_setting .__descr__ {
  font: 400 13px/26px "Roboto","Arial","Open Sans",sans-serif;
  padding: 4px 13px;
  color : var(--hxrymz_color_base_label); 
}


