

.gg{
  color:var(--hxrymz_color_base_hover);
--hxrymz_color_base_hover:#ff7817;
--hxrymz_color_base_label:#6c6c6c;
--hxrymz_panel_background_color: #f9f9f9;
--hxrymz_color_base_detail_label:#454545;
--hxrymz_color_base_detail_dark_label:#151515;
--hxrymz_color_base_detail_gray_label:#aaaaaa;

}

.option__edit_payments{
  background-color: var(--hxrymz_panel_background_color);
}

.option__edit_payments .__header__{
  display: flex;
}

.option__edit_payments .__header__ svg{
  fill: var(--hxrymz_color_base_detail_label);
}

.option__edit_payments .__title_body__{
  color: var(--hxrymz_color_base_detail_label);
  font: 500 23px/29px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;  
  margin: 0 7px 12px;
}

.option__edit_payments .__title_description__{
  color: var(--hxrymz_color_base_label); 
  font: 400 15px/26px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;  
}


.option__edit_payments .__minimun__pay__Wrapper__{
  margin: 35vw auto 15vw;
  width: 180px;
}

.option__edit_payments .__minimun__pay__Wrapper__ .__minimun__pay__{
  color: var(--hxrymz_color_base_hover);
  font: 600 45px/55px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;  
}

.option__edit_payments .__minimun__pay__Wrapper__ .__btns__{
  display: flex;
  margin: 13vw 0 13vw;
}

.option__edit_payments .__minimun__pay__Wrapper__ .__btn__{
  border-radius:50%;
  border: 1px solid var(--hxrymz_color_base_hover);
  border: 1px solid rgba(255,120,23,0.3);
  width: 24px;
  height: 24px;
  padding: 6px;
}

.option__edit_payments .__minimun__pay__Wrapper__ .__btn__ svg{  
  fill: var(--hxrymz_color_base_hover);
}

.btnSavePayments{
  border-radius:7px;
  border: 1px solid #d2d2d2;
  width: 168px;
  padding: 6px;
}

.btnSavePayments span{
  color: var(--hxrymz_color_base_detail_label);
  font: 600 15px/25px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;  
}

.option__edit_payments .__minimun__pay__Wrapper__ .__minimun__pay__ input[type="number"]{
  border: none;
  text-align: center;
  color: var(--hxrymz_color_base_hover);
  font: 600 45px/55px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
  outline: none; 
  max-width: 168px; 
}


