.radioBtnHRM_wrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.radioBtnHRM{
  display: flex;
  color: var(--disable-color-radioBtnHRM);;
  text-transform: capitalize;
  position: relative;
}

.radioBtnHRM span{
  margin-left: 8px;
  color: var(--disable-color-radioBtnHRM);;
}

.radioBtnHRM ._icon_checked_ svg{
  fill: var(--disable-color-radioBtnHRM);;
  transition: fill  250ms ease;
}

.radioBtnHRM ._icon_checked_ svg path {
  transition: d 0.25s ease;
}


.radioBtnHRM._active ._icon_checked_ svg{
  fill: var(--active-color-radioBtnHRM);
}
