
._DateModal_{
    background-color: #fff; 
    border-radius: 13px;
    position: relative;
    padding: 0px 0px;
}


.headerDateModal{    
    background-color: var(--hxrymz_color_base_hover);
    color: #fff;
    padding: 24px;
    -webkit-border-radius: 12px 12px 0 0;
    border-radius: 12px 12px 0 0;    
}

.headerDateModal  .Title{
        -webkit-box-flex: 1;
        box-flex: 1;
        -webkit-flex-grow: 1;
        flex-grow: 1;
        -webkit-flex-shrink: 1;
        flex-shrink: 1;
        overflow: hidden;
        word-wrap: break-word;    
}

.headerDateModal .Title .date{    
        font-size: 14px;
        font-weight: normal;
        padding-top: 10px;
        opacity: .7;    
}



.bodyDateModal{
    overflow: visible;
    background-color: #fff;
    position: relative;
    -webkit-box-flex: 2;
    box-flex: 2;
    -webkit-flex-grow: 2;
    flex-grow: 2;
    -webkit-flex-shrink: 2;
    flex-shrink: 2;
    display: block;
    font: 400 14px/20px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
    padding: 0 24px;
    overflow-y: auto;
}



.formCont {
    overflow: visible;
    padding-top: 15px;
    white-space: nowrap;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
}

.inputDatePick {
    display: inline-block;
    vertical-align: bottom;
    width: 55px;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
    display: inline-block;
    outline: none;
    padding-bottom: 8px;
    width: 60px;
}


.inputDatePick .base {
    padding-top: 24px;
    height: 40px;
    position: relative;
    vertical-align: top;
    max-width: 55px;
}

.inputDatePick .base .date {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    position: relative;
    top: 14px;
}

.inputDatePick .base .date .inputCont{
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-flex: 1;
    box-flex: 1;
    -webkit-flex-grow: 1;
    flex-grow: 1;
    -webkit-flex-shrink: 1;
    flex-shrink: 1;
    min-width: 0%;
    position: relative;
}

.inputDatePick .base .date .inputCont input {
    -webkit-box-flex: 1;
    box-flex: 1;
    -webkit-flex-grow: 1;
    flex-grow: 1;
    -webkit-flex-shrink: 1;
    flex-shrink: 1;
    background-color: transparent;
    border: none;
    display: block;
    font: 400 16px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
    height: 24px;
    line-height: 24px;
    margin: 0;
    min-width: 0%;
    outline: none;
    padding: 0;
    text-align: center;
    z-index: 0;    
}

.inputDatePick .base .date .inputCont input[type=number]::-webkit-inner-spin-button, 
.inputDatePick .base .date .inputCont input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}


.inputDatePick .base .date .inputCont .label {
    -webkit-transform: scale(.75) translateY(-39px);
    transform: scale(.75) translateY(-39px);
}
.inputDatePick .base .date .inputCont .label {
    -webkit-transform-origin: bottom left;
    transform-origin: bottom left;
    -webkit-transition: all .3s cubic-bezier(0.4,0,0.2,1);
    transition: all .3s cubic-bezier(0.4,0,0.2,1);
    -webkit-transition-property: color,bottom,transform;
    transition-property: color,bottom,transform;
    color: rgba(0,0,0,0.38);
    font: 400 16px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
    font-size: 16px;
    pointer-events: none;
    position: absolute;
    bottom: 3px;
    left: 0;
    width: 100%;
}

.borderBottom {
    background-color: rgba(0,0,0,0.12);
    bottom: -2px;
    height: 1px;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    width: 100%;
}


.borderBottomActive {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    background-color:var(--hxrymz_color_base_hover);
    bottom: -2px;
    height: 2px;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    width: 100%;
    -webkit-animation: transform .3s cubic-bezier(0.4,0,0.2,1);
    animation: transform .3s cubic-bezier(0.4,0,0.2,1);
}

.borderBottomActive.load {
    transform: scaleX(1);
    -webkit-transform: scaleX(1);
}



@-webkit-keyframes removeBorderBottom{0%{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:1}to{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:0}}




.actionContainer {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-shrink: 0;
    flex-shrink: 0;
    box-pack: end;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    padding: 24px 24px 16px 24px;
}

.actionContainer .button {
    -webkit-user-select: none;
    -webkit-transition: background .2s .1s;
    transition: background .2s .1s;
    border: 0;
    -webkit-border-radius: 2px;
    border-radius: 2px;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    min-width: 4em;
    outline: none;
    overflow: hidden;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    -webkit-tap-highlight-color: transparent;
    z-index: 0;
    color:var(--hxrymz_color_base_hover);
}

.actionContainer .button .cc{
    margin: 8px;
    font-weight: 600;
    line-height: 20px;
}

.actionContainer .button.disable{
    color:rgba(0,0,0,0.12);
}
