



._squedule_days_{
  color: #7e7e7e;
  font: 600 11px/14px Roboto,RobotoDraft,Helvetica,Arial,sans-serif; 
  width: 53%;
  text-align: right;
}



._squedule_day_item{
  padding: 9px 13px;
    margin: 2px 0 2px 15px;
    background-color: transparent;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    height: 24px;
    display: flex;
}


._squedule_day_Name__ {
  margin: 0px 13px;
  font: 500 13px/26px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
  color: #5b5b5b;
  text-transform: capitalize;
  width: 120px;
  text-align: left;
  padding: 5px 0 0 5px;
}


._squedule_day_Hour__{
  width: 30px;
  padding: 5px 0 0 5px;
  margin: 0px 13px 0 0;
}

._squedule_day_Hour__ svg{
  fill: #7a7a7a;
}

.__hourly__Wrapper__{
  display: flex;
}

.__hourly__Wrapper__ .__minimun__pay__Wrapper__{
  max-width: 120px;
  margin: 15vw auto 15vw;
}


.btnSave_Wrpp{
  max-width: 180px;
  margin: 0 auto;
}


.___hourly__label__{
  margin: 0px 13px 35px;
  font: 500 17px/26px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
  color: #5b5b5b;
  text-transform: capitalize;
  width: 80px;
  text-align: center;
  padding: 5px 0 0 5px;
}