.autoStatus{

}

.setting--cards.automaticGrabber ._actions_{
  padding: 8px 0 0;
}

.setting--cards.automaticGrabber ._label_{
  
  padding: 10px 0 0 10px;
}

.autoStatus.isValidTk svg{
  fill: var(--color-base--hover);
  animation: rotate2 2.5s ease-in-out infinite;
  stroke-linecap: round;
}

.autoStatus.isNotValidTk svg{
  fill: #b2b2b2;
  
}





@keyframes rotate2 {
  100% {
    transform: rotate(360deg);
  }
}



