  background-color: #fff;
  border-top-left-radius: 13px;
  border-top-right-radius: 13px;
  min-height: 70vh;
}

.option--wrapper .__header--{
  padding: 7px 13px;
  border-bottom: 1px solid #aaa;
  display: flex;
}

.option--wrapper .__header-- h5{
