      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.5;
    }
  }




  @-webkit-keyframes IconSpriteAll {
    0% {
      -webkit-transform: scale(0.2);
      opacity: 0.1;
    }
    50% {
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1.2);
