.toggle-container.ptoggle-button {
  display: inline-block;
  position: relative;
  width: 36px;
  height: 14px;
  margin: 4px 1px;
  cursor: pointer;
}

.toggle-label.ptoggle-button {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  pointer-events: none;
  color: navy;
}


.toggle-bar.ptoggle-button {
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 8px;
  pointer-events: none;
  opacity: 0.4;
  -webkit-transition: background-color linear .08s;
  transition: background-color linear .08s;
  background-color:  #000000;
  
}

.toggle-button.ptoggle-button {
  -webkit-transform: translate(0px, 0);
  transform: translate(0px, 0);
  background-color: var(--checkBox--button--color);
}

.toggle-button.ptoggle-button {
  position: absolute;
  top: -3px;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.6);
  transition: -webkit-transform linear .08s, background-color linear .08s;
  -webkit-transition: background-color linear .08s, -webkit-transform linear .08s;
  transition: background-color linear .08s, -webkit-transform linear .08s;
  transition: transform linear .08s, background-color linear .08s;
  transition: transform linear .08s, background-color linear .08s, -webkit-transform linear .08s;
  will-change: transform;
  background-color: #fafafa;
}

.toggle-container._active .toggle-button.ptoggle-button {
  -webkit-transform: translate(16px, 0);
  transform: translate(16px, 0);
  background-color:var(--checkBox--button--Active--color);
}


.toggle-container._active .toggle-bar.ptoggle-button {
  opacity: 0.5;
  background-color:var(--checkBox--button--Active--color);
}


