.inputDatePick .base .date .inputCont .label {
    -webkit-transform: scale(.75) translateY(-39px);
    transform: scale(.75) translateY(-39px);
}
.inputDatePick .base .date .inputCont .label {
    -webkit-transform-origin: bottom left;
    transform-origin: bottom left;
    -webkit-transition: all .3s cubic-bezier(0.4,0,0.2,1);
    transition: all .3s cubic-bezier(0.4,0,0.2,1);
    -webkit-transition-property: color,bottom,transform;
    -webkit-transition-property: color,bottom,-webkit-transform;
    transition-property: color,bottom,-webkit-transform;
    transition-property: color,bottom,transform;
    transition-property: color,bottom,transform,-webkit-transform;
    color: rgba(0,0,0,0.38);
    font: 400 16px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
    font-size: 16px;
    pointer-events: none;
    position: absolute;
    bottom: 3px;
    left: 0;
    width: 100%;
}

.borderBottom {
    background-color: rgba(0,0,0,0.12);
    bottom: -2px;
    height: 1px;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    width: 100%;
}


.borderBottomActive {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    background-color:var(--hxrymz_color_base_hover);
    bottom: -2px;
    height: 2px;
    left: 0;
    margin: 0;
    padding: 0;
    position: absolute;
    width: 100%;
    -webkit-animation: transform .3s cubic-bezier(0.4,0,0.2,1);
    animation: transform .3s cubic-bezier(0.4,0,0.2,1);
}

.borderBottomActive.load {
    transform: scaleX(1);
    -webkit-transform: scaleX(1);
}



@-webkit-keyframes removeBorderBottom{0%{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:1}to{-webkit-transform:scaleX(1);transform:scaleX(1);opacity:0}}




