
.filter--cards--wrapers{ 
  display: block;
}

.filter--cards{  
  padding: 15px 4px;
  margin: 15px 0;
  border-bottom: 1px solid #b2b2b2;
}


.filter--cards:last-child {
  border-bottom: 1px solid transparent;
}



.top--secction{
  text-align: left;
  display: flex;
}

.filter--cards .active--secction .hrs-show{  
  max-height: 60px;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
  cursor: pointer;
  padding: 10px 0px;
  font: 600 17px/19px "Google Sans","Roboto","Arial","Open Sans",sans-serif;
  color: #4c4c4c;
  text-align: right;
}

.filter--cards .active--secction .hourly-show{  
  max-height: 60px;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
  cursor: pointer;
  padding: 10px 0px;
  font: 600 15px/19px "Google Sans","Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;
  text-align: right;
  float: right;
  margin-right: 12px;
}



.filter--cards .top--secction .actions .assign-block{
  transform: scale(0);
  opacity: 0; 
  max-height: 0px;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
}




.filter--cards .top--secction.editing .actions .assign-block{
  transform: scale(1);
  opacity: 1; 
  max-height: 60px;
}


.assign-block ._header{
  display: flex;
  margin: 6px 0;
}

.assign-block ._header span{
  text-transform: capitalize;
  font: 600 15px/19px "Google Sans","Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;
}
.assign-block ._header ._close{
  font: 600 17px/19px "Google Sans","Roboto","Arial","Open Sans",sans-serif;
  color: #4c4c4c;
}




