  opacity: 0;
  min-height: 160px;
  max-height: 160px;
  transform: translate3d(0, 100%, 0);
  background: #fff;
  /* padding: 15px; */
  transition: transform  ease-in-out .48s,  opacity ease-in-out .48s;
}


.__action_options--.__active .__days__filters__{      
  opacity: 1;
  transform: translate3d(0, 0%, 0);      
}

.__action_options--.__active .__srvarea__filters__{      
