 .printcallout {
  background-color: var(--printcolor-error-light);
  color: var(--printcolor-error-text);
}






 .printcallout {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 0 0 10px;
  position: relative;
  margin: 0 0 24px;
  min-height: 48px;
}

 .printcallout .printcallout-body  {
  display: flex;
}


 .printcallout .printcallout-body .printcallout-message {
  margin-right: 24px;
  padding: 12px 0;
  white-space: normal;
  font: 500 14px/20px Roboto,"Roboto",sans-serif;
}

 .printcallout .printcallout-body .printcallout-icon { 
  padding: 18px 12px 0;
}
 .printcallout .printcallout-body .printcallout-icon.error { 
  padding: 10px 12px 0;
}








 .printcallout._success{ 
  background-color:#e6f4ea;
  color: #34a853;
}

 .printcallout._success .printcallout-body .printcallout-icon svg{
  fill: #34a853;
}




 .printcallout._problem{ 
  background-color: #fce8e6;
  color: #ea4335;
}


 .printcallout._problem .printcallout-body .printcallout-icon svg{
  fill: #ea4335;
}




 .printcallout._info{ 
  background-color: #fef7e0;
  color: #fbbc04;
}


 .printcallout._info .printcallout-body .printcallout-icon svg{
  fill: #fbbc04;
}



.printcallout._infoGreen{ 
  background-color: rgba(0,164,180,0.15);
  color: #00a4b4;
}


 .printcallout._infoGreen .printcallout-body .printcallout-icon svg{
  fill: #00a4b4;
}




.cncBtn a{
  text-decoration: none;
  color: #6f6f6f;
  font: 600 15px/26px "Google Sans", Roboto, Arial, sans-serif;
  letter-spacing: 1.1px;
}